  methods: {
    copyCode() {
      const code = `document.addEventListener('paste', e => e.stopImmediatePropagation(), true);`;
      try {
        navigator.clipboard
          .writeText(code)
          .then(() => this.isCopied = true)
          .then(() => setTimeout(() => this.isCopied = false, 2e3))
          .catch(console.error)
        ;
      } catch(err) {
        console.error(err);
      }
    },
  },
